.front-image-container {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: calc(100vh - 67px);
    width: 100%;
    background-color: #414042;
    overflow: hidden;
  }
  
  .front-image-img {
    height: 100%;
    min-width: 100%;
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
  }
  
  .front-image-content-desktop {
    position: absolute;
    bottom: 9%;
    left: 3%;
    padding: 50px;
    border-radius: 5px;
    background: linear-gradient(145deg, var(--main-app-color), var(--main-deg-app-color));
    transition: transform 200ms ease-in-out;
    transform-origin: center;
    cursor: pointer;
    opacity: 0.9;
    text-align: center;
  }
  
  .front-image-content-desktop:hover {
    transform: scale(1.05);
    box-shadow: 20px 20px 60px var(--main-deg-app-color), -20px -20px 60px var(--main-deg-app-color);
  }
  
  .front-image-btn {
    position: absolute;
    left: 99%;
    bottom: 0%;
    font-size: xx-large;
    color: white;
    border-radius: 5px;
    padding: 10px;
    background: var(--main-deg-app-color);
  }
  
  .front-image-content-text {
    font-size: 35px;
    color: white;
  }
  
  .front-image-instantly-loved-text {
    font-weight: bolder;
  }
  